import { Add } from '@mui/icons-material';
import { createTheme, Tab, Tabs, ThemeProvider } from '@mui/material';
import React, { useState } from 'react'
import { pricingImg } from '../../../assets/Home';

const theme = createTheme({
    palette: {
        primary: {
            main: '#EB2131',
        },
        secondary: {
            main: '#FFDE59',
        },
    }
});

const Pricing = () => {
    const [tabIndex, setTabIndex] = useState(0);

    const handleTabChange = (_, newIndex) => {
        setTabIndex(newIndex);
    };
    return (
        <ThemeProvider theme={theme}>
            <div className='flex justify-center w-full mt-28'>
                <div className='w-[92vw] lg:w-[60vw] 2xl:w-[1500px]'>
                    <Tabs value={tabIndex} onChange={handleTabChange} centered>
                        <Tab label="Delivery Price" />
                        <Tab label="Storage Price" />
                    </Tabs>
                    <div className={`grid grid-cols-2 gap-8 items-center mt-7 `}>

                        <div className='w-full'>
                            {tabIndex === 0 ? (
                                <div className='bg-[#FFDE59] p-5 rounded-lg'>
                                    <h3 className='text-xl font-bold underline '>Our Delivery Price:</h3>
                                    <h5 className='mt-3 text-xl font-semibold'>$34.99</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"First 2 bag(s): Up to 30 Miles"}</p>
                                    <Add className='!text-[#EB2131] !font-bold ml-2' fontSize='small' />
                                    <h5 className='text-xl font-semibold'> $10 Per Bag</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"3-8 bag"}</p>
                                    <Add className='!text-[#EB2131] !font-bold ml-2' fontSize='small' />
                                    <h5 className='text-xl font-semibold'> $1 Per Mile</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"31-250 Miles"}</p>
                                </div>
                            ) : (
                                <div className='bg-[#FFDE59] p-5 rounded-lg'>
                                    <h3 className='text-xl font-bold underline '>Our Storage Price:</h3>
                                    <h5 className='mt-3 text-xl font-semibold'>$9.99</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"Per Suitcase (24hrs Only)"}</p>
                                    <Add className='!text-[#EB2131] !font-bold ml-2' fontSize='small' />
                                    <h5 className='text-xl font-semibold'> $5.99</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"Per Carry On/Personal (24hrs Only)"}</p>
                                    <Add className='!text-[#EB2131] !font-bold ml-2' fontSize='small' />
                                    <h5 className='text-xl font-semibold'> $2 OFF Daily Price</h5>
                                    <p className='text-gray-600 text-sm font-medium'>{"Long Term Storage up to 30 days"}</p>
                                </div>
                            )}
                        </div>

                        <div>
                            <img src={pricingImg} alt="Payment Methods" className='ml-auto h-[270px]' />
                        </div>

                    </div>
                </div>
            </div>
        </ThemeProvider>
    )
}

export default Pricing
