import React from 'react';
import { icon1, icon2, icon3, icon4 } from "../../../assets/partner";
const reasons = [
    {
        id: 1,
        title: "Skip the Baggage Claim",
        description: "Our service allows guests to bypass the tedious baggage claim process, eliminating one of the most stressful aspects of travel. We take charge of the luggage right from the airport, ensuring a smooth transition and delivering it directly to your guests' destination. This seamless process enables your guests to start enjoying their vacation immediately, without the delay and inconvenience of waiting at the baggage claim area. Our efficient and professional service guarantees that the luggage is handled with the utmost care, ensuring its safety and security throughout the journey. This provides your guests with peace of mind, knowing that their belongings are in safe hands, and enhances their overall travel experience by adding an extra layer of convenience and comfort from the moment they arrive.",
        icon: icon1,
        number: "01"
    },
    {
        id: 2,
        title: "Convenient Luggage Pick-Up",
        description: "We offer a seamless pick-up service at the end of your guests' stay, ensuring that their luggage is collected from their accommodation and transported back to the airport on time. This convenient service allows your guests to fully enjoy their final moments without the hassle and worry of handling their bags. Our commitment to punctuality and efficiency guarantees that guests can have a smooth and organized departure. We manage the logistics to ensure that the luggage is securely handled and arrives at the airport in perfect condition. This provides peace of mind for your guests, knowing that their belongings are in safe hands and ready for their journey home.",
        icon: icon2,
        number: "02"
    },
    {
        id: 3,
        title: "Flexible Luggage Storage",
        description: "For guests with late flights or layovers, we provide secure luggage storage solutions. This service allows them to explore the city or attend to other activities without the burden of their luggage, making their trip more enjoyable and stress-free. Our storage facilities are secure and easily accessible, ensuring that guests can retrieve their belongings whenever they need, thus enhancing their overall travel experience. Our storage facilities are secure and easily accessible, ensuring that guests can retrieve their belongings whenever they need. This flexibility and convenience enhance their overall travel experience, allowing them to make the most of their time without worrying about their luggage. By taking care of their belongings, we help them focus on enjoying their journey.",
        icon: icon3,
        number: "03"
    },
    {
        id: 4,
        title: "Lost and Found Assistance",
        description: "Our dedicated Lost and Found service ensures the efficient recovery and return of lost items to their owners, saving time and resources while enhancing guest satisfaction. We promptly address the stress of losing personal belongings, using a systematic approach to track and recover items. Whether it's luggage or a forgotten item, we handle each case with care. By providing this service, we offer guests peace of mind, knowing their lost items will be returned quickly. Our commitment to customer satisfaction and attention to detail sets us apart, ensuring a positive guest experience and fostering trust and loyalty.",
        icon: icon4,
        number: "04"
    },
];

const Reasons = () => {
    return (
        <div className="mt-20">
            <h2 className="text-4xl font-bold text-center mb-8">Why Choose <span className="text-red-600">Us</span></h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 px-8">
                {reasons.map(reason => (
                    <div key={reason.id} className="relative p-8 bg-white rounded-lg shadow-lg hover:shadow-xl duration-300 transform hover:translate-y-1 workprocess-single-item transition-all">
                        <div className="absolute text-7xl font-bold text-gray-200 bottom-0 opacity-75 right-0 !z-0">{reason.number}</div>
                        <div className="flex items-center mb-4 z-10">
                            <div className="flex items-center justify-center w-12 h-12 bg-red-600 text-white rounded-full workprocess-icon-box transition-transform duration-300">
                                <img src={reason.icon} alt={reason.title} className="h-8 w-8 object-contain" />
                            </div>
                            <h3 className="text-xl font-semibold ml-4">{reason.title}</h3>
                        </div>
                        <p className="text-gray-700 z-10">
                            {reason.description}
                        </p>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Reasons;






