import React, { useEffect } from 'react';
import { Companies, FAQ, Footer, Header, PartnerForm, PartnerHero, Reasons } from "../../Component";

const Partner = () => {
  useEffect(() => {
    const partnerForm = document.getElementById('partner');

    const handleScroll = (entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          document.body.style.transition = 'background-color 1s ease'; // Smooth transition
          document.body.style.backgroundColor = '#FFDE59';
        } else {
          document.body.style.transition = 'background-color 1s ease'; // Smooth transition
          document.body.style.backgroundColor = 'white';
        }
      });
    };

    const observer = new IntersectionObserver(handleScroll, {
      threshold: 0.2, // Adjust threshold as needed
    });

    if (partnerForm) {
      observer.observe(partnerForm);
    }

    return () => {
      if (partnerForm) {
        observer.unobserve(partnerForm);
      }
    };
  }, []);

  return (
    <div>
      <Header partner />
      <PartnerHero />
      <Companies />
      <Reasons />
      {/* <PartnerForm /> */}
      <FAQ />
      <Footer />
    </div>
  );
}

export default Partner;
