import React from 'react'
import { coffee, ction, hyatt, marriott, parkmerced, related, twilio, veritas } from '../../../assets/partner'

const Companies = () => {
    return (
        <section className='mt-20 h-[90vh] w-full bg-[#FFDE59] flex justify-center items-center'>
            <div className='w-full'>
                <p className='text-white font-medium text-xl uppercase tracking-[0.2em] text-center'>Companies we work with</p>
                <div className='mt-14 grid grid-cols-4 grid-rows-2 gap-5 w-[83%] h-[320px] mx-auto'>
                    {[veritas, parkmerced, ction, related, twilio, coffee, marriott, hyatt].map(img => (
                        <div className='bg-white rounded-2xl flex justify-center items-center ' key={img}>
                            <img src={img} alt={img} className={`${img === ction && "h-[70%]"} ${img === parkmerced && "h-[70%]"} w-[70%] object-contain`} />
                        </div>
                    ))}
                </div>
            </div>
        </section>
    )
}

export default Companies