import React, { useState } from 'react';
import { Box, Container, Typography, Accordion, AccordionSummary, AccordionDetails, TextField, Button } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const FAQ = () => {
  const [expanded, setExpanded] = useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Box sx={{ color: '#fff', py: 8 }} id="partner" className="!mt-20">
      <Container maxWidth="md">
        <Typography variant="h4" component="h2" gutterBottom align="center" sx={{ mb: 8 }}>
          Frequently Asked Questions
        </Typography>
        <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} sx={{ backgroundColor: '#333', color: '#fff', mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}>
            <Typography>What is BaggageClaimed?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              BaggageClaimed offers VIP luggage storage and transfer services at multiple airports.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')} sx={{ backgroundColor: '#333', color: '#fff', mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}>
            <Typography>How much does BaggageClaimed cost?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              The cost depends on the specific services you choose. Contact us for detailed pricing.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')} sx={{ backgroundColor: '#333', color: '#fff', mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}>
            <Typography>Where can I use BaggageClaimed services?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              Our services are available at multiple airports. Visit our locations page for more details.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')} sx={{ backgroundColor: '#333', color: '#fff', mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}>
            <Typography>How do I book a service?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              You can book our services online through our website or contact our support team.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion expanded={expanded === 'panel5'} onChange={handleChange('panel5')} sx={{ backgroundColor: '#333', color: '#fff', mb: 2 }}>
          <AccordionSummary expandIcon={<ExpandMoreIcon sx={{ color: '#fff' }} />}>
            <Typography>What if I have special requirements?</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              We offer custom solutions to fit your needs. Reach out to our team for more information.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Box sx={{ mt: 8, textAlign: 'center' }}>
          <Typography variant="h6" component="p" gutterBottom>
            Ready to get started? Enter your email to create or restart your membership.
          </Typography>
          <Box component="form" noValidate autoComplete="off" sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
            <TextField
              required
              id="email"
              label="Email address"
              variant="outlined"
              sx={{ backgroundColor: '#fff', borderRadius: 1, mr: 2, width: '300px' }}
            />
            <Button variant="contained" color="primary" sx={{ height: '56px' }}>
              Get Started
            </Button>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default FAQ;
